.pop-up {
  background-color: var(--black);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: var(--white);
  padding: 0;
  opacity: 1;
  z-index: 1000;
  overflow: auto;
  animation-name: show;
  animation-duration: .3s;

  @keyframes show {
    from {
      transform: translateX(100%);
      opacity: 0;
    }
    to {
      transform: translateX(0%);
      opacity: 1;
    }
    
  }
  &.closed {
    animation-name: hide;
    animation-duration: .3s;
  }

  // @keyframes hide {
  //   from {
  //     transform: translateX(0);
  //     opacity: 1;
  //   }
  //   to {
  //     transform: translateX(100%);
  //     opacity: 0;
  //   }
  // }

  // &.active {
  //   left: 0;
  //   opacity: 0.99;
  // }

  .pop-up-wrapper{
    display: flex;
    height: 100vh;
    width: 100%;
    justify-content: space-between;
    button {
      position: fixed;
      top: 15%;
      margin-right: 2rem;
      right: 35%;
      font-size: 2rem;
      font-weight: 700;
      color: var(--white);
      background-color: transparent;
      border: none;
      cursor: pointer;
      @media screen and (max-width: 500px) {
        right: 1rem;
      }
    }
    .pop-up-img {
      align-self: flex-end;
      max-width: 30%;
      height: 100%;
      img {
        height: 100%;
        object-fit: cover;

        @media screen and (max-width: 500px) {
          display: none;
        }
      }
    }
    .pop-up-text {
      padding-top: 16rem;
      padding-left: 2rem;
      h2 {
        color: var(--white);
      }
    }
  }
}
